<template>
  <div
    id="summary"
    class="app-container content-container"
  >
    <div>
      <h1>Fajnie, e wiesz!</h1>

      <p>
        <b>
          Mamy nadziej, e teraz ju wiesz, jak rozpoznawa fake newsy.
          Czytajc informacje w internecie, pamitaj o najwaniejszych zasadach!
        </b>
      </p>

      <ol>
        <li>
          Zwracaj uwag na podejrzane i nieznane rdo informacji.
        </li>
        <li>
          Pamitaj, e zdjcie umieszczone w artykule lub mem rwnie moe by fake newsem.
        </li>
        <li>
          Zwracaj uwag na wpisy skrajnie emocjonalne, liczne wykrzykniki,
          pytajniki czy due litery.
        </li>
      </ol>
    </div>
    <div class="buttons">
      <router-link
        tag="button"
        type="button"
        name="button"
        class="opaque-blue"
        to="/share-results"
      >
        Dalej
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Summary',
};
</script>

<style scoped>
#summary {
  color: var(--color-blue);
  background: #EBF1F4;
  position: relative;
}
.buttons {
  display: flex;
  width: 100%;
  z-index: 10;
}
.buttons a {
  margin-top: 1em;
}
</style>



